import Vue from 'vue';

const incomeStore = {
	namespaced: true,
	state: {
		currentIncome: {},
		income: {
			2019: {},
			2020: {
				1: {
					category: 'Зарплата',
					currency: "RUB",
					date: "20.01.2020, 00:00:00",
					dateTime: "20.01.2020, 10:00:00",
					value: 40000,
					commentary: 'Зпшка',
					dataFull: 'Thu Jan 20 2020 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				2: {
					category: 'Аванс',
					currency: "RUB",
					date: "30.01.2020, 00:00:00",
					dateTime: "30.01.2020, 10:00:00",
					value: 30000,
					commentary: 'Аванс',
					dataFull: 'Thu Jan 30 2020 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
			},
			2021: {
				1: {
					category: 'Зарплата',
					currency: "RUB",
					date: "01.10.2021, 00:00:00",
					dateTime: "01.10.2021, 11:00:00",
					value: 40000,
					commentary: 'Зпшка октябрь',
					dataFull: 'Thu Oct 01 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				2: {
					category: 'Аванс',
					currency: "RUB",
					date: "31.10.2021, 00:00:00",
					dateTime: "31.10.2021, 09:00:00",
					value: 20000,
					commentary: 'Мини зпшка сентябрь',
					dataFull: 'Thu Oct 31 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
				3: {
					category: 'Аванс',
					currency: "RUB",
					date: "30.08.2021, 00:00:00",
					dateTime: "30.08.2021, 09:00:00",
					value: 20000,
					commentary: 'Мини зпшка апрель',
					dataFull: 'Thu Apr 22 2021 00:00:00 GMT+0300 (Москва, стандартное время)',
				},
			},
			2022: {},
		},
		optionsCategory: [
			{ text: "Зарплата", value: "Зарплата" },
			{ text: "Премия", value: "Премия" },
		],
		currentYear: (new Date()).getFullYear(),
		yearSelectedByTheUser: (new Date()).getFullYear(),
	},
	getters: {
		getCurrentYear(state) {
			// Возвращаю текущий год
			return state.currentYear;
		},
		filterArr(state) {
			// Возвращаю все доходы за нужный юзеру год

			Object.keys(state.income).forEach(el => {
				if (el == state.yearSelectedByTheUser) {
					state.currentIncome = state.income[el]
				}
			})

			return state.currentIncome;
		},
		getIncome(state) {
			// Прокидываю данные в виде массива с объектами
			Object.keys(state.income).forEach(el => {
				if (el == state.currentYear) {
					state.currentIncome = state.income[el]
				}
			})

			return state.currentIncome
		},
		getIncomeAmount(state, getters) {
			// Расчет доходов за месяц
			let date = new Date().toLocaleString().split(',')[0].slice(3, 5);

			let sum = 0;
			Object.values(getters.getIncome).forEach(el => {
				if (date == el.date.split(',')[0].slice(3, 5)) {
					sum += el.value;
				}
			})
			return sum
		},
		getOptionsCategory(state) {
			// Прокидываю категории в селекте доходов
			return state.optionsCategory;
		},
		getFullIncomeYears(state) {
			// Полная сумма доходов за всё время
			let arr = [];
			Object.values(state.income).forEach(el => {
				arr.push(Object.values(el).reduce((sum, elem) => sum + elem.value, 0));
			})

			return arr.reduce((sum, elem) => sum += elem);
		},
		getFullIncome(state, getters) {
			// Полная сумма доходов за год
			return Object.values(state.currentIncome).reduce((sum, el) => sum += el.value, 0);
		},
		getFullIncomeArrMonths(state, getters) {
			// Полная сумма доходов за каждый месяц
			let arr = [];

			function f1(value) {
				let sum = 0;
				let obj = {};

				Object.values(getters.filterArr).forEach(el => {
					console.log()
					if (el.date.split(',')[0].slice(3, 5) == value) {
						sum += el.value;
					}
				})
				obj[value] = sum;
				return obj;
			}

			arr.push(f1('01'))
			arr.push(f1('02'))
			arr.push(f1('03'))
			arr.push(f1('04'))
			arr.push(f1('05'))
			arr.push(f1('06'))
			arr.push(f1('07'))
			arr.push(f1('08'))
			arr.push(f1('09'))
			arr.push(f1('10'))
			arr.push(f1('11'))
			arr.push(f1('12'))

			return arr;

		}
	},
	mutations: {
		ADD_NEW_INCOME(state, income) {
			// Записываю новый доход в исходный объект
			Vue.set(state.income[(new Date()).getFullYear()], income.id, income);
		},
		ADD_NEW_YEAR(state, year) {
			state.yearSelectedByTheUser = year;
		}
	},
	actions: {
		setNewIncome(context, newIncome) {
			// Получаю данные из формы введенные пользователем, добавляю им id и прокидываю дальше в мутации для дальнейшей записи в исходный объект
			const newIncomePlusId = { ...newIncome, id: Object.values(context.state.income[(new Date()).getFullYear()]).length + 1 };
			context.commit('ADD_NEW_INCOME', newIncomePlusId);
		},
		setNextYear2(context, year) {
			context.commit('ADD_NEW_YEAR', year)
		},
		setPreviousYear2(context, year) {
			context.commit('ADD_NEW_YEAR', year)
		},
	}
};

export default incomeStore;