import Vue from 'vue'
import App from './App.vue'
import './plugins/elements';
import store from "./store";
import VueRouter from 'vue-router';
import router from './router/router';
import vuetify from './vuetify/vuetify'

Vue.config.productionTip = false

Vue.use(VueRouter);

new Vue({
  store,
  router,
  vuetify,
  render: h => h(App),
}).$mount('#app')

