<template>
  <div class="container">
    <div class="section__ind new">
      <el-header>
        <el-page-header
          content="Баланс за год"
          title="Назад"
          @back="$router.push({ name: 'Main' })"
        ></el-page-header>
      </el-header>

      <div class="years">
        <button @click="previousYear">Предыдущий год</button>
        <button @click="nextYear">Следующий год</button>
      </div>

      <el-carousel
        arrow="always"
        :autoplay="false"
        indicator-position="none"
        height="700px"
      >
        <el-carousel-item v-for="item in 1" :key="item">
          <h3>{{ year }}</h3>
          <div class="carousel__full-wrap">
            <div class="carousel__full">
              <h4>Всего:</h4>
              <p>{{ getFullIncome - getFullCost }}</p>
            </div>
          </div>
          <div class="carousel__wrap">
            <div class="carousel__month">
              <div v-for="item in month" :key="item">
                <div>
                  <p>{{ item }}</p>
                </div>
              </div>
            </div>
            <div class="carousel__val">
              <div v-for="item in getBalanceMonth" :key="item.key">
                <p>{{ item }}</p>
              </div>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "BalanceOut",
  components: {},
  data: () => ({
    year: 2021,
    month: [
      "Январь",
      "Февраль",
      "Март",
      "Апрель",
      "Май",
      "Июнь",
      "Июль",
      "Август",
      "Сентябрь",
      "Октябрь",
      "Ноябрь",
      "Декабрь",
    ],
  }),
  computed: {
    ...mapGetters("income", ["getFullIncomeArrMonths", "getFullIncome"]),
    ...mapGetters("expenses", [
      "getFullExpensesArrMonths",
      "getFullCost",
      "filterArr",
      "getCurrentYear",
    ]),
    getBalanceMonth(getFullIncomeArrMonths, getFullExpensesArrMonths) {
      let rez = [];
      let sum = 0;
      this.getFullIncomeArrMonths.forEach((elem, i) => {
        Object.values(elem).forEach((el, k) => {
          sum = el - Object.values(this.getFullExpensesArrMonths[i])[k];
          rez.push(sum);
        });
      });
      return rez;
    },
  },
  methods: {
    ...mapActions("expenses", ["setPreviousYear", "setNextYear"]),
    ...mapActions("income", ["setPreviousYear2", "setNextYear2"]),
    previousYear() {
      this.year--;
      this.setPreviousYear(this.year);
      this.setPreviousYear2(this.year);
    },
    nextYear() {
      this.year++;
      this.setNextYear(this.year);
      this.setNextYear2(this.year);
    },
  },
};
</script>

<style>
.years {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
}

.el-carousel__arrow {
  top: 10%;
}
.el-carousel__item h3 {
  padding: 20px;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.carousel__wrap {
  display: flex;
  padding: 10px 20px 40px 20px;
}
.carousel__wrap p {
  margin: 5px 0;
  border-bottom: 1px solid #333;
  width: 100%;
  padding-bottom: 10px;
}
.carousel__month {
  flex: 0 1 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.carousel__val {
  flex: 0 1 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.carousel__val div,
.carousel__month div {
  width: 100%;
}
.carousel__val div p {
  text-align: right;
}
.carousel__full-wrap {
  margin-top: 40px;
  padding: 15px 20px;
  background-color: rgb(70, 196, 185);
}
.carousel__full {
  display: flex;
  justify-content: space-between;
}
</style>
