<template>
  <div class="container">
    <div class="section__ind new">
      <el-header>
        <el-page-header
          content="Записать новый расход"
          title="Назад"
          @back="$router.push({ name: 'Main' })"
        ></el-page-header>
      </el-header>
      <!-- <v-app>
        <v-avatar color="primary" size="56">123</v-avatar>
      </v-app> -->

      <el-card>
        <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2">
          <el-row :gutter="10">
            <el-form-item class="el-form-item-custom" label="Дата" required>
              <el-col :xs="24" :sm="11" :md="11" :lg="11" :xl="11">
                <el-form-item prop="date1" class="ind">
                  <el-date-picker
                    type="date"
                    placeholder="Pick a date"
                    v-model="ruleForm2.date1"
                    style="width: 100%"
                  ></el-date-picker>
                </el-form-item>
              </el-col>
              <el-col
                class="line hidden-xs-only"
                :xs="2"
                :sm="2"
                :md="2"
                :lg="2"
                :xl="2"
                >-</el-col
              >
              <el-col :xs="24" :sm="11" :md="11" :lg="11" :xl="11">
                <el-form-item prop="date2">
                  <el-time-picker
                    placeholder="Pick a time"
                    v-model="ruleForm2.date2"
                    style="width: 100%"
                  ></el-time-picker>
                </el-form-item>
              </el-col>
            </el-form-item>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item label="Категория" prop="category">
                <el-select
                  v-model="ruleForm2.category"
                  placeholder="Список категорий"
                  class="w100"
                >
                  <el-option
                    v-for="item in getOptionsCategory2"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item class="ind" label="Название новой категории">
                <el-input
                  v-model="valueNewCategory2"
                  style="margin-bottom: 20px"
                ></el-input>
                <el-button type="primary" @click="addNewCategory2"
                  >Добавить новую категорию</el-button
                >
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item class="ind" label="Сумма" prop="value">
                <el-input v-model.number="ruleForm2.value"></el-input>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item label="Валюта" prop="currency">
                <el-select
                  v-model="ruleForm2.currency"
                  placeholder="Список валют"
                  class="w100"
                >
                  <el-option label="RUB" value="RUB"></el-option>
                  <el-option label="UAH" value="UAH"></el-option>
                  <el-option label="USD" value="USD"></el-option>
                  <el-option label="EUR" value="EUR"></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item label="Комментарий" prop="сommentary">
                <el-input
                  type="textarea"
                  v-model="ruleForm2.сommentary"
                ></el-input>
              </el-form-item>
            </el-col>

            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
              <el-form-item>
                <el-button type="primary" @click="submitForm2('ruleForm2')"
                  >Записать</el-button
                >
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
// import Income from "@/components/Income";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "new",
  components: {
    // Income,
  },
  data: () => ({
    ruleForm2: {
      value: "",
      date1: "",
      date2: "",
      category: "",
      currency: "",
      сommentary: "",
    },
    rules2: {
      value: [
        {
          type: "number",
          required: true,
          message: "Введите цифры",
          trigger: "change",
        },
      ],
      date1: [
        {
          type: "date",
          required: true,
          message: "Выберите дату",
          trigger: "change",
        },
      ],
      date2: [
        {
          type: "date",
          required: true,
          message: "Выберите время",
          trigger: "change",
        },
      ],
      category: [
        {
          required: true,
          message: "Выберите категорию",
          trigger: "change",
        },
      ],
      currency: [
        {
          required: true,
          message: "Выберите валюту",
          trigger: "change",
        },
      ],
      сommentary: [
        {
          required: false,
          message: "Введите комментарий",
          trigger: "change",
        },
      ],
    },
    valueNewCategory2: "",
  }),
  computed: {
    ...mapGetters("expenses", ["getOptionsCategory2"]),
  },
  methods: {
    ...mapActions("expenses", ["setNewExpenses2"]),
    submitForm2() {
      this.$refs.ruleForm2.validate((valid) => {
        if (valid) {
          let newObj = this.ruleForm2;
          let dataFull = JSON.parse(JSON.stringify(newObj.date1));
          newObj.date = newObj.date1.toLocaleString();
          newObj.dateTime = newObj.date2.toLocaleString();
          let newObjFull = {
            ...newObj,
            dataFull,
          };
          this.setNewExpenses2(newObjFull);
          this.$router.push({ name: "main" });
        } else {
          console.log("false");
          return false;
        }
      });
    },
    addNewCategory2() {
      this.getOptionsCategory2.push({
        text: this.valueNewCategory2,
        value: this.valueNewCategory2,
      });
      this.valueNewCategory2 = "";
    },
  },
};
</script>

